.roadmap {
  margin-bottom: 60px;

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;

    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    min-height: 820px;
    transition: all .2s;

    &:hover {
      border-color: var(--color-accent);
    }

    &-list {
      padding: 0;
      margin: 0 0 40px;

      li {
        list-style: none;
        position: relative;

        &::before {
          content: '';
          position: absolute;
          left: -30px;
          top: 0;
        }

        &:not(:last-of-type) {
          margin-bottom: 24px;
        }
      }
    }

    &-title {
      margin: 0 0 8px;
    }

    &-date {
      color: #484D80;
      transition: all .2s;
      margin: 0;
    }

    &:hover &-date {
      color: var(--color-accent);
    }
  }
}
