.main-menu {
  ul {
    display: flex;
    padding: 0;
    margin: 0;

    li {
      list-style: none;
      font-size: 18px;
      transition: all 0.2s ease-in 0s;

      &:not(:last-of-type) {
        margin-right: 56px;
      }

      a {
        color: white;
        text-decoration: none;
        transition: all .1s;
        position: relative;
        cursor: pointer;        
        padding-bottom: 19px;

        &.expanded {
          // &:after {
          //   content: '';
          //   display: block;
          //   width: 3px;
          //   height: 3px;
          //   border-radius: 1px;
          //   background-color: var(--color-hover);
          //   position: absolute;
          //   top: 0;
          //   right: -7px;
          // }

          // &:hover:after {
          //   background-color: var(--color-accent);
          // }

          // &.active:after {
          //   background-color: var(--color-accent);
          // }
        }

        &:hover {
          color: var(--color-hover);
        }

        &.active {
          border-bottom: var(--color-accent) 2px solid;
        }
      }      
    }
  }
}
