.about-token {
  margin-bottom: 24px;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    min-height: 810px;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: image-set(
      url('../../images/Backgrounds/About/about-token-bg.jpg') 1x,
      url('../../images/Backgrounds/About/about-token-bg@2x.jpg') 2x,
    );
  }

  &__title {
    margin: 40px 0 0;
  }

  &__text {
    width: 45%;
    // margin: 0;

    @media (max-width: 768px) {
      width: 100%;
    }
  }
}
