.about-others {
  margin-bottom: 24px;

  &__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 24px;

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
    }
  }

  &__title {
    margin: 0 0 20px;

    a {
      color: white;
      text-decoration: none;

      &:after {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
      }
    }
  }

  &__subtitle {
    margin: 0;
  }

  &__card {
    min-height: 394px;
    transition: all .2s;
    position: relative;

    &:not(.about-others__card_disabled):hover {
      border-color: var(--color-accent);
    }

    &_disabled {
      opacity: .5;
    }
  }
}
