.faq {
  margin-bottom: 60px;

  &__title {
    margin: 0 0 30px;
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;

    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    display: flex;
    align-items: flex-end;
    position: relative;
    min-height: 440px;
    transition: all .2s;

    &:hover {
      background-color: var(--color-hover);
    }

    &-title {
      margin: 0;

      a {
        color: white;
        text-decoration: none;

        &::after {
          content: '';
          display: block;
          width: 100%;
          height: 100%;
          position: absolute;
          left: 0;
          top: 0;
        }
      }
    }
  }
}
