.secondary-title {
  font-size: 52px;
  font-weight: 400;
  color: white;
  line-height: 1.2;
  transition: all 0.2s ease-in 0s;

  @media (max-width: 1024px) {
    font-size: 32px;
  }

  a {
    color: white;
    text-decoration: none;
    transition: all .2s;
    
    &:hover {
      color: var(--color-hover);
    }
  }
}
