.they-talk {
  position: relative;
  margin-bottom: 24px;

  &__container {
    display: flex;
    align-items: flex-end;
    position: relative;
    padding-left: 22px;

    @media (max-width: 768px) {
      flex-direction: column;
      align-items: flex-start;
    }

    &::before {
      content: '';
      display: block;
      width: 78px;
      height: 160px;
      background: linear-gradient(to right, #0D1033 0%, #0D1033 21.88%, rgba(15, 24, 55, 0) 100%);
      position: absolute;
      left: 300px;
      top: 0;
      z-index: 8888;

      @media (max-width: 1366px) {
        left: 270px;
      }

      @media (max-width: 1024px) {
        left: 202px;
      }
    }

    &::after {
      content: '';
      display: block;
      width: 78px;
      height: 160px;
      background: linear-gradient(to left, #0D1033 0%, #0D1033 21.88%, rgba(15, 24, 55, 0) 100%);
      position: absolute;
      right: -80px;
      top: 0;
      z-index: 8888;
    }

    &::before,
    &::after {
      @media (max-width: 768px) {
        display: none;
      }
    }
  }

  &__title {
    margin: 0 30px 10px 0;

    @media (max-width: 768px) {
      width: fit-content;
      margin: 0 0 24px;
    }
  }

  &__slider {
    display: flex;
    justify-content: stretch;
    width: 100%;
    position: relative;
    right: -78px;
    // overflow-x: scroll;
    overflow: hidden;
    padding-left: 60px;

    &::-webkit-scrollbar {
      display: none;
    }

    @media (max-width: 1366px) {
      right: -40px;
    }

    @media (max-width: 768px) {
      right: auto;
      overflow: visible;
      padding-left: 0;
    }
  }

  &__slide {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: none;
    position: relative;
    height: 160px;
    width: 330px;
    border-radius: 9px;
    border: rgba($color: #fff, $alpha: 0.1) 1px solid;
    transition: all .2s;
    cursor: pointer;
    text-decoration: none;
    color: white;
    padding: 20px;
    margin-right: 24px;

    &:hover {
      background-color: var(--color-hover);
      border: var(--color-hover) 1px solid;
    }

    &-date {
      position: absolute;
      left: 0;
      top: 0;
    }

    &-description {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      font-size: 14px;
      line-height: 1.6;
      color: white;
      transition: all 0.2s ease-in 0s;
      text-overflow: ellipsis;
      overflow: hidden;
      margin: 0;
    }

    &-img {
      height: 20px;
    }

    &-logo-name {
      margin-right: 10px;
    }

    &-bottom {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      position: relative;
      font-size: 16px;
      color: rgba($color: white, $alpha: .3);
      transition: all .3s;
      padding-left: 65px;
    }
  }

  &__navigation-buttons {
    display: flex;
    position: absolute;
    bottom: 10px;
    left: 200px;
    width: 100%;
    z-index: 9999;

    .nav-button {
      position: relative;
      top: -110px;
      background-color: rgba($color: #13173E, $alpha: .9);

      &_next {
        margin-left: 4px;
      }

      &.swiper-button-disabled {
        opacity: .5;
      }
    }

    @media (max-width: 1024px) {
      left: 120px;
    }

    @media (max-width: 768px) {
      display: none;
    }
  }
}
